import React, {  useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import Card from '../../../shared/components/UIElements/Card';
import ErrorModal from '../../../shared/components/UIElements/ErrorModal';
import LoadingSpinner from '../../../shared/components/UIElements/LoadingSpinner';
// import { AuthContext } from '../../../shared/context/auth-context';
import { useHttpClient } from '../../../shared/hooks/http-hook';
import Sellerproductlist from './Sellerproductlist';
import './Sellerproductlist.css'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


const Selleruploadedproduct = () => {

    const[loadedUsers, setLoadedUsers]= useState([])
    const { isLoading, error, sendRequest, clearError } = useHttpClient();
    // const auth= useContext(AuthContext)
  // const navigate= useNavigate()
  const userId= useParams().uid;

    useEffect(() => {
        const sendrequest = async () => {
          // setIsLoading(true);       
          try {
            const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL +`/places/user/${userId}`);
      
           setLoadedUsers(responseData.places);
            // auth.customerlogin(responseData.customer.id)
            console.log(responseData.places)
          } catch (err) {}
        };
        sendrequest();
      }, [sendRequest, userId]);
      

      if (loadedUsers.length === 0) {
        return (
          <div className="center">
            <Card>
              <h2 style={{"color":"white", "textDecoration":"none"}}>No Uploaded Product Found.</h2>
            </Card>
          </div>
        );
      }
  return (
    <>
        
    <ErrorModal error={error} onClear={clearError} />
        {isLoading && <LoadingSpinner asOverlay />}


        <Container fluid>
      <Row style={{border:"1px solid black"}}>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>IMAGE</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>NAME</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>TITLE</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>DESCRIPTION</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>PRICE</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>EDIT</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center"}}>DELETE</Col>
      </Row>
    </Container>


{
  loadedUsers.map((p)=>{
    return <Sellerproductlist
    key={p.id}
    id={p.id}
    name={p.name}
      title = {p.title}
      image={p.image}
      description= {p.description}
      price={p.price}
      time={p.time}
    />
  }
  )
}

    </>
  )
}

export default Selleruploadedproduct
